/* Foundation */
.bsw-weak {
  filter: invert(80%);
}
.bsw-hide {
  display: none;
}
.bsw-foundation,
.bsw-body,
.bsw-loading,
.bsw-loading > .ant-spin-container {
  height: 100%;
}
.bsw-page-loading {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 1000;
  letter-spacing: 3px;
}
.bsw-page-loading span {
  position: absolute;
  font-size: 18px;
  font-weight: lighter;
  top: 38.2%;
  left: 50%;
  color: rgba(0, 0, 0, 0.4);
  transform: translateY(-50%) translateX(-50%);
  animation: bsw-breathing-middle 2s infinite;
}
.bsw-debug {
  padding: 10px;
  border: 10px #e2e1e1 solid;
}
.bsw-code {
  display: inline-flex;
  margin: 0 1px;
  background: rgba(242, 244, 245, 0.5);
  padding: 0.2em 0.4em;
  border-radius: 3px;
  font-size: 0.9em;
  border: 1px solid #eee;
}
.bsw-code.full {
  padding: 8px 10px;
  width: 99.6%;
}
/* Cover */
.ant-spin-nested-loading > div > .ant-spin {
  z-index: 1000;
  position: fixed;
}
.ant-tooltip {
  z-index: 998;
}
.ant-spin-blur:after {
  opacity: .2;
}
.ant-spin-container:after {
  background: #000000;
}
div.ant-modal-mask {
  background-color: rgba(0, 0, 0, 0.2);
}
/* Animate */
@keyframes bsw-breathing {
  0% {
    opacity: .1;
  }
  45%,
  65% {
    opacity: 1;
  }
  100% {
    opacity: .1;
  }
}
@keyframes bsw-breathing-middle {
  0% {
    opacity: .2;
  }
  50% {
    opacity: .6;
  }
  100% {
    opacity: .2;
  }
}
@keyframes bsw-rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* Theme for tips */
.tippy-popper {
  max-width: 500px;
}
.tippy-popper .tippy-tooltip.light-theme {
  color: rgba(0, 0, 0, 0.51);
  text-align: left;
  box-shadow: 0 0 10px 2px rgba(154, 161, 177, 0.2), 0 2px 40px -4px rgba(36, 40, 47, 0.2), 0 2px 4px -2px rgba(91, 94, 105, 0.2);
  background-color: #fff;
}
.tippy-popper .tippy-tooltip.light-theme[data-animatefill] {
  background-color: transparent;
}
.tippy-popper .tippy-tooltip.light-theme .tippy-backdrop {
  background-color: #fff;
}
.tippy-popper .tippy-tooltip.light-theme .tippy-roundarrow {
  width: 24px;
  height: 24px;
  fill: #fff;
}
.tippy-popper[x-placement^=top] .tippy-tooltip.light-theme .tippy-arrow {
  border-top: 7px solid #fff;
  border-right: 7px solid transparent;
  border-left: 7px solid transparent;
}
.tippy-popper[x-placement^=bottom] .tippy-tooltip.light-theme .tippy-arrow {
  border-bottom: 7px solid #fff;
  border-right: 7px solid transparent;
  border-left: 7px solid transparent;
}
.tippy-popper[x-placement^=left] .tippy-tooltip.light-theme .tippy-arrow {
  border-left: 7px solid #fff;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
}
.tippy-popper[x-placement^=right] .tippy-tooltip.light-theme .tippy-arrow {
  border-right: 7px solid #fff;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
}
